<template>
<div class="indexBox">
    <div class="index-tp">
        <div class="tp-search">
            <div class="tp-search-box">
                <div class="iconfont icon-sousuo"></div>
                <input type="text" v-model="tableNo" class="inputCla" placeholder="床位检索">
                <div class="iconfont icon-quxiao" @click="deletInput" v-show="showDelet"></div>
            </div>
            <div class="tp-right">
                <span v-if="isShowSearch" class="search-btn" @click="searchFun()">搜索</span>
                <span v-else class="sales-points-container" @click="salesPointsShow()">
            <div class="iconfont icon-didian"></div>营业点
          </span>
            </div>
        </div>
        <div class="tp-list" v-if="itemActive!=4">
            <div class="tp-item" :class="itemActive==1?'tp-item-active':''" @click="orderSwitch('1')">全部订单</div>
            <div class="tp-item tp-item-has" :class="itemActive==2?'tp-item-active':''" @click="orderSwitch('2')">
                <i class="tp-item-icon"></i>有订单
            </div>
            <div class="tp-item tp-item-no" :class="itemActive==3?'tp-item-active':''" @click="orderSwitch('3')">
                <i class="tp-item-icon"></i>无订单
            </div>
        </div>
    </div>
    <div class="index-ct">
        <mt-loadmore :top-method="loadTop" @top-status-change="handleTopChange" :topDistance="40" :maxDistance="100" :top-status.sync="topStatus" ref="loadmore">
            <div class="index-ct-box" v-if="itemActive==1">
                <div v-if="SeatsAllArr.length>0">
                    
                        <div class="list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
                            <div v-for="(item, index) in SeatsAllArr" :key="index" class="table-box" @click="gotoCreateOrder(item)">
                                <div class="table-top" :class="[item.hasOrder?'table-top-has':'table-top-no']">{{item.hasOrder?'有订单':'无订单'}}</div>
                                <div class="table-num">{{item.code}}</div>
                                <div class="table-name">{{item.pcDescript}}</div>
                            </div>
                        </div>
                   
                </div>
                <div v-else-if="SeatsAllArr.length ==0 && isShowLoadFail" class="no-list">
                    <div class="no-result-icon">
                        <i class="iconfont icon-jiazaishibai"></i>
                    </div>
                    <p class="no-result-text">未查到结果</p>
                </div>
            </div>

            <div class="index-ct-box" v-if="itemActive==2">
                <div v-if="SeatsHasArr.length>0">
                 
                        <div class="list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
                            <div v-for="(item, index) in SeatsHasArr" :key="index" class="table-box" @click="gotoCreateOrder(item)">
                                <div class="table-top table-top-has">有订单</div>
                                <div class="table-num">{{item.code}}</div>
                                <div class="table-name">{{item.pcDescript}}</div>
                            </div>
                            <!-- <div class="table-box">
              <div class="table-top table-top-has">使用</div>
              <div class="table-num">A001</div>
              <div class="table-name">中餐厅</div>
              </div>-->
                        </div>
                    
                </div>
                <div v-else class="no-list">
                    <div class="no-result-icon">
                        <i class="iconfont icon-jiazaishibai"></i>
                    </div>
                    <p class="no-result-text">未查到结果</p>
                </div>
            </div>

            <div class="index-ct-box" v-if="itemActive==3">
                <div v-if="SeatsNotHasArr.length>0">
                  
                        <div class="list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
                            <div v-for="(item, index) in SeatsNotHasArr" :key="index" class="table-box" @click="gotoCreateOrder(item)">
                                <div class="table-top table-top-no">无订单</div>
                                <div class="table-num">{{item.code}}</div>
                                <div class="table-name">{{item.pcDescript}}</div>
                            </div>
                        </div>

                </div>
                <div v-else-if="SeatsNotHasArr.length ==0 && isShowLoadFail" class="no-list">
                    <div class="no-result-icon">
                        <i class="iconfont icon-jiazaishibai"></i>
                    </div>
                    <p class="no-result-text">未查到结果</p>
                </div>
            </div>
            <div class="index-ct-box index-ct-box-result" v-if="itemActive==4">
                <div v-if="searchResultArr.length>0">
                    <div class="list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
                        <div v-for="(item, index) in searchResultArr" :key="index" class="table-box" @click="gotoCreateOrder(item)">
                            <div class="table-top" :class="[item.hasOrder?'table-top-has':'table-top-no']">{{item.hasOrder?'有订单':'无订单'}}</div>
                            <div class="table-num">{{item.code}}</div>
                            <div class="table-name">{{item.pcDescript}}</div>
                        </div>
                    </div>
                </div>
                <div v-else-if="this.searchResultArr.length ==0 && this.isShowLoadFail" class="no-list">
                    <div class="no-result-icon">
                        <i class="iconfont icon-jiazaishibai"></i>
                    </div>
                    <p class="no-result-text">未查到结果</p>
                </div>
            </div>
            <div slot="top" class="mint-loadmore-top" :class="{ 'rotop': topStatus === 'drop' }" >
                <span v-show="topStatus !== 'loading'"
              :class="{ 'rotate': topStatus === 'drop' }"> ↓ 下拉刷新</span>
                <span v-show="topStatus === 'loading'">Loading...</span>
            </div>
        </mt-loadmore>
    </div>
    <transition name="salesPoints">
        <food-sales-points class="salesPointsCon" v-show="isShowSalesPoints" @SalesPointsHide="SalesPointsHide($event)"></food-sales-points>
    </transition>
    <footTabs></footTabs>
</div>
</template>

<script>
import {
    InfiniteScroll,
    Toast,
    Loadmore
} from "mint-ui";
import footTabs from "../common/footTabs";
import foodSalesPoints from "../common/foodSalesPoints";
export default {
    name: "index",
    components: {
        footTabs,
        "food-sales-points": foodSalesPoints
    },
    data() {
        return {
            topStatus: "",
            tableNo: "",
            showDelet: false,
            isShowSearch: false,
            itemActive: 1,
            isShowSalesPoints: false,
            SalesPointsStr: "",
            SeatsAllArr: [],
            SeatsHasArr: [],
            SeatsNotHasArr: [],
            searchResultArr: [],
            pageNum: 1,
            isShowLoadFail: false
        };
    },
    watch: {
        tableNo() {
            if (this.tableNo) {
                this.showDelet = true;
                this.isShowSearch = true;
            } else {
                this.showDelet = false;
                this.isShowSearch = false;
                this.itemActive = 1;
            }
        }
    },
    created() {
        this.$store.commit("reset");
        this.$store.commit("clearOrderNum");
        this.getAjax(null, this.pageNum, null, null, this.SeatsAllArr);
    },
    methods: {
        orderSwitch(val) {
            if (this.itemActive == val) {
                return;
            }
            this.isShowLoadFail = false;
            this.itemActive = val;
            this.pageNum = 1;
            if (this.itemActive == 1) {
                this.SeatsAllArr = [];
                if (this.SalesPointsStr) {
                    this.getAjax(
                        this.SalesPointsStr,
                        this.pageNum,
                        null,
                        null,
                        this.SeatsAllArr
                    );
                } else {
                    this.getAjax(null, this.pageNum, null, null, this.SeatsAllArr);
                }
            } else if (this.itemActive == 2) {
                this.SeatsHasArr = [];
                if (this.SalesPointsStr) {
                    this.getAjax(
                        this.SalesPointsStr,
                        this.pageNum,
                        true,
                        null,
                        this.SeatsHasArr
                    );
                } else {
                    this.getAjax(null, this.pageNum, true, null, this.SeatsHasArr);
                }
            } else {
                this.SeatsNotHasArr = [];
                if (this.SalesPointsStr) {
                    this.getAjax(
                        this.SalesPointsStr,
                        this.pageNum,
                        false,
                        null,
                        this.SeatsNotHasArr
                    );
                } else {
                    this.getAjax(null, this.pageNum, false, null, this.SeatsNotHasArr);
                }
            }
        },
        //列表点击
        gotoCreateOrder(item) {
            this.$store.commit("submitSeatsInfo", item);
            this.$router.push("/foodCreateOrder");
        },
        //input删除键
        deletInput() {
            this.tableNo = "";
            this.searchResultArr = [];
        },
        salesPointsShow() {
            this.isShowSalesPoints = true;
        },
        SalesPointsHide(arr) {
            console.log("arr:", arr);
            this.isShowLoadFail = false;
            this.isShowSalesPoints = false;
            if (this.SalesPointsStr != arr) {
                this.SalesPointsStr = arr;

                this.pageNum = 1;
                this.SeatsAllArr = [];
                this.SeatsHasArr = [];
                this.SeatsNotHasArr = [];
                if (this.itemActive == 1) {
                    this.getAjax(
                        this.SalesPointsStr,
                        this.pageNum,
                        null,
                        null,
                        this.SeatsAllArr
                    );
                } else if (this.itemActive == 2) {
                    this.getAjax(
                        this.SalesPointsStr,
                        this.pageNum,
                        true,
                        null,
                        this.SeatsHasArr
                    );
                } else {
                    this.getAjax(
                        this.SalesPointsStr,
                        this.pageNum,
                        false,
                        null,
                        this.SeatsNotHasArr
                    );
                }
            }
        },
        searchFun() {
            this.searchResultArr = [];
            this.itemActive = 4;
            this.pageNum = 0;
            this.getAjax(
                null,
                this.pageNum,
                null,
                this.tableNo,
                this.searchResultArr
            );
        },
        loadMore() {
            // debugger;
            //this.loading = true;
            if (this.itemActive == 1) {
                if (this.SalesPointsStr) {
                    this.getAjax(
                        this.SalesPointsStr,
                        ++this.pageNum,
                        null,
                        null,
                        this.SeatsAllArr
                    );
                } else {
                    this.getAjax(null, ++this.pageNum, null, null, this.SeatsAllArr);
                }
            } else if (this.itemActive == 2) {
                if (this.SalesPointsStr) {
                    this.getAjax(
                        this.SalesPointsStr,
                        ++this.pageNum,
                        true,
                        null,
                        this.SeatsHasArr
                    );
                } else {
                    this.getAjax(null, ++this.pageNum, true, null, this.SeatsHasArr);
                }
            } else {
                if (this.SalesPointsStr) {
                    this.getAjax(
                        this.SalesPointsStr,
                        ++this.pageNum,
                        false,
                        null,
                        this.SeatsNotHasArr
                    );
                } else {
                    this.getAjax(null, ++this.pageNum, false, null, this.SeatsNotHasArr);
                }
            }
        },
        handleTopChange: function (status) {
            this.topStatus = status;
        },
        getAjax(pccodes, page, hasOrder, tableNo, PointsArr) {
            let params;
            if (pccodes) {
                params = {
                    method: "getTableMaps",
                    pccodes: this.SalesPointsStr,
                    page: page,
                    pageSize: 40,
                    hasOrder: hasOrder,
                    tableNo: tableNo
                };
            } else {
                let foodUserInfo = JSON.parse(localStorage.foodUserInfo);
                params = {
                    method: "getTableMaps",
                    workStationCode: foodUserInfo.workStationCode,
                    page: page,
                    pageSize: 40,
                    hasOrder: hasOrder,
                    tableNo: tableNo
                };
            }

            this.$http
                .get("/ohotel/router", {
                    params: params,
                    vv: 5
                })
                .then(res => {
                    if (res.data.code == "0") {
                        PointsArr.push(...res.data.data);
                        this.isShowLoadFail = true;
                    } else {
                        this.msg(res.data.msg);
                    }
                });
        },
        loadTop() {
            console.log("ddd");
            this.pageNum = 1;
            this.SeatsAllArr = [];
            this.SeatsHasArr = [];
            this.SeatsNotHasArr = [];
            if (this.itemActive == 1) {
                this.getAjax(
                    this.SalesPointsStr,
                    this.pageNum,
                    null,
                    null,
                    this.SeatsAllArr
                );
            } else if (this.itemActive == 2) {
                this.getAjax(
                    this.SalesPointsStr,
                    this.pageNum,
                    true,
                    null,
                    this.SeatsHasArr
                );
            } else {
                this.getAjax(
                    this.SalesPointsStr,
                    this.pageNum,
                    false,
                    null,
                    this.SeatsNotHasArr
                );
            }
            this.$refs.loadmore.onTopLoaded();
        },
        msg(msg) {
            Toast({
                message: msg,
                position: "bottom",
                duration: 3000
            });
        }
    }
};
</script>

<style lang="less">
.indexBox {
    .index-tp {
        width: 100vw;
        position: fixed;
        top: 0;
        z-index: 1;
        background-color: #f4f5f7;

        .tp-search {
            display: flex;
            justify-content: space-between;
            flex-direction: row;
            padding-left: 0.373rem;
            padding-right: 0.373rem;
            background-color: #393c41;

            .tp-search-box {
                flex: 1;
                height: 0.853rem;
                margin-top: 0.211rem;
                display: flex;
                justify-content: flex-start;
                flex-direction: row;
                align-items: center;
                background: #f6f8f9;
                border-radius: 1.3rem;

                .icon-sousuo {
                    color: #cccccc;
                    margin-left: 0.27rem;
                    font-size: 0.37rem;
                }

                .icon-quxiao {
                    color: #cccccc;
                    font-size: 0.32rem;
                    margin-right: 0.27rem;
                }

                .inputCla {
                    border: none;
                    background: #f6f8f9;
                    outline: none;
                    font-size: 0.32rem;
                    width: 80%;
                    margin-left: 0.27rem;
                }
            }

            .tp-right {
                margin-left: 0.67rem;
                width: 1.493rem;
                height: 1.28rem;

                .search-btn {
                    display: inline-block;
                    width: 1.493rem;
                    height: 1.28rem;
                    line-height: 1.28rem;
                    color: #fff;
                }

                .sales-points-container {
                    height: 1.28rem;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    flex-direction: row;
                    font-size: 0.32rem;
                    color: #fff;
                }
            }
        }

        .tp-list {
            padding-left: 0.373rem;
            padding-right: 0.373rem;
            padding-top: 0.2rem;
            margin-top: 0.133rem;
            background-color: #fff;
            display: flex;
            align-items: center;
            justify-content: space-around;
            font-size: 0.32rem;
            color: #333333;

            .tp-item {
                padding-bottom: 5px;
                border-bottom: 3px solid #fff;
                display: flex;
                align-items: center;

                .tp-item-icon {
                    margin-right: 3px;
                    width: 0.185rem;
                    height: 0.163rem;
                    border-radius: 2.667rem;
                }
            }

            .tp-item-has .tp-item-icon {
                background: #00cbcb;
            }

            .tp-item-no .tp-item-icon {
                background: #e7737e;
            }

            .tp-item-active {
                border-bottom-color: #f69c2d;
            }
        }
    }

    .index-ct {
        min-height: 84vh;

        .index-ct-box {
            padding-left: 0.293rem;
            padding-right: 0.293rem;
            border-radius: 1.235rem;
            text-align: left;
            padding-bottom: 1.6rem;
            font-size: 0;
            margin-top: 85px;

            .no-list {
                font-size: 0.32rem;

                .no-result-icon {
                    text-align: center;
                    padding-top: 2.667rem;

                    .iconfont {
                        font-size: 1.28rem;
                    }
                }

                .no-result-text {
                    text-align: center;
                }
            }
        }

        .index-ct-box-result {
            margin-top: 45px;
        }

        .list {
            width: 100%;

            .table-box:nth-child(4n) {
                margin-right: 0px;
            }

            .table-box {
                width: 23%;
                height: 2.213rem;
                background: #fff;
                margin: 0.27rem 2.5% 0 0;
                border-radius: 0.123rem;
                display: inline-block;

                .table-top {
                    height: 0.533rem;
                    line-height: 0.533rem;
                    border-radius: 0.123rem 0.123rem 0 0;
                    font-size: 0.293rem;
                    color: #ffffff;
                    letter-spacing: 0;
                    text-align: center;
                }

                .table-top-has {
                    background: #00cbcb;
                }

                .table-top-no {
                    background: #e7737e;
                }

                .table-num {
                    font-size: 0.373rem;
                    font-size: 18.48px;
                    color: #333333;
                    letter-spacing: 0;
                    text-align: center;
                    margin-top: 0.27rem;
                }

                .table-name {
                    font-size: 13.86px;
                    color: #333333;
                    letter-spacing: 0;
                    text-align: center;
                }
            }
        }
    }

    .salesPointsCon {
        position: fixed;
        top: 0px;
        left: 0px;
        z-index: 9;
    }

    .salesPoints-enter-active,
    .salesPoints-leave-active {
        transition: all 0.5s;
    }

    .salesPoints-enter,
    .salesPoints-leave-to {
        margin-left: 100%;
    }
    .rotop {padding-top:80px;margin-bottom:-50px;}
}
</style>



