<template>
  <div class="salesPoints">
    <foodHeaderNav @hidePageFun="hideSalesPoints()" title="营业点" eventShow="true"></foodHeaderNav>
    <div class="body">
      <p class="title">选择营业点</p>
      <div class="point-list">
        <span
          class="point-item"
          v-for="(item, index) in salesPointsArr"
          :key="index"
          @click="choseFun(index)"
          :class="[item.chose?'point-item-active':'']"
        >{{item.descript}}</span>
        <!-- <span class="point-item point-item-active">咖啡厅</span>
        <span class="point-item">咖啡厅</span>
        <span class="point-item">咖啡厅</span>
        <span class="point-item">咖啡厅</span>-->
      </div>
    </div>
    <div class="foot" @click="okFun()">确 定</div>
  </div>
</template>

<script>
import { Indicator, Toast } from "mint-ui";
import foodHeaderNav from "../common/foodHeaderNav";
export default {
  components: {
    foodHeaderNav
  },
  data() {
    return {
      salesPointsArr: [],
      SelectedArr: []
    };
  },
  created() {
    let foodUserInfo = JSON.parse(localStorage.foodUserInfo);
    this.$http
      .get("/ohotel/router", {
        params: {
          method: "getPcCodes",
          workStationCode: foodUserInfo.workStationCode
        },
        vv: 5
      })
      .then(res => {
        console.log("res:", res);
        if (res.data.code == "0") {
          // this.$router.push("/foodSite");
          this.salesPointsArr = res.data.data;
          this.salesPointsArr.forEach(ele => {
            this.$set(ele, "chose", false);
          });
        } else {
          this.msg(res.data.msg);
        }
      });
  },
  methods: {
    hideSalesPoints() {
      this.salesPointsArr.forEach(element => {
        if (this.SelectedArr.indexOf(element.code) > -1) {
          element.chose = true;
        } else {
          element.chose = false;
        }
      });
      this.$emit("SalesPointsHide", this.SelectedArr.join(","));
    },
    okFun() {
      this.SelectedArr = [];
      this.salesPointsArr.forEach(element => {
        if (element.chose) {
          this.SelectedArr.push(element.code);
        }
      });
      this.$emit("SalesPointsHide", this.SelectedArr.join(","));
    },
    choseFun(index) {
      console.log("index:", index);
      if (this.salesPointsArr[index].chose) {
        this.salesPointsArr[index].chose = false;
      } else {
        this.salesPointsArr[index].chose = true;
      }

      console.log("salesPointsArr:", this.salesPointsArr);
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    }
  }
};
</script>
<style lang="less" scoped>
.salesPoints {
  width: 100%;
  height: 100%;
  background: #f4f5f7;

  .body {
    width: 100%;
    background-color: #fff;
    padding: 0 0.4rem;
    margin-top: 0.133rem;
    box-sizing: border-box;
    .title {
      height: 33px;
      line-height: 33px;
      font-size: 0.32rem;
      color: #666666;
      text-align: left;
      border-bottom: 1px solid #eeeeee;
    }
    .point-list {
      padding: 0.4rem 0;
      text-align: left;
      .point-item {
        display: inline-block;
        padding: 5px 0.507rem;
        margin-right: 0.16rem;
        margin-bottom: 0.267rem;
        background: #f6f8f9;
      }
      .point-item-active {
        color: #fff;
        background: #ff8000;
      }
    }
  }
  .foot {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 1.2rem;
    line-height: 1.2rem;
    font-size: 0.427rem;
    color: #ffffff;
    text-align: center;
    background-image: linear-gradient(-90deg, #f6a129 0%, #ff6e61 98%);
  }
}
</style>




